}

.projects-three__carousel.owl-theme .owl-nav .owl-prev {
    height: 82px;
    width: 82px;
    line-height: 82px;
    border-radius: 0;
    color: var(--tecture-white);
    background-color: var(--tecture-base);
    font-size: 22px;
    margin: 0;
    text-align: center;
    clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
}

.projects-three__carousel.owl-theme .owl-nav .owl-next {
    margin-left: 0px;
}

.projects-three__carousel.owl-theme .owl-nav .owl-prev {
    margin-right: 0px;
}

.projects-three__carousel.owl-theme .owl-nav .owl-next span,
.projects-three__carousel.owl-theme .owl-nav .owl-prev span {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.projects-three__carousel.owl-theme .owl-nav .owl-next:hover,
.projects-three__carousel.owl-theme .owl-nav .owl-prev:hover {
    background-color: var(--tecture-white);
    color: var(--tecture-base);
}

/*--------------------------------------------------------------
# Project Four
--------------------------------------------------------------*/
.projects-four {
    position: relative;
    display: block;
    padding: 120px 0 0;
}

/*--------------------------------------------------------------
# Project Details
--------------------------------------------------------------*/
.project-details {
    position: relative;
    display: block;
    counter-reset: count;
    padding: 120px 0 113px;
    z-index: 1;
}

.project-details__img {
    position: relative;
    display: block;
}

.project-details__img img {
    width: 100%;
}

.project-details__content {
    position: relative;
    display: block;
    margin-top: 48px;
}

.project-details__title-1 {
    font-size: 30px;
    font-weight: 700;
    line-height: 40px;
    text-transform: uppercase;
}

.project-details__text-1 {
    margin-top: 24px;
    margin-bottom: 28px;
}

.project-details__img-and-faq {
    position: relative;
    display: block;
    margin-top: 57px;
    margin-bottom: 43px;
}

.project-details__img-box-img {
    position: relative;
    display: block;
}

.project-details__img-box-img img {
    width: 100%;
}

.project-details__faq-box {
    position: relative;
    display: block;
}

.project-details__faq-box .faq-one-accrodion .accrodion {
    position: relative;
    display: block;
    border-radius: 3px;
    background-color: #0e110d;
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
}

.project-details__faq-box .faq-one-accrodion .accrodion-title {
    position: relative;
    display: block;
    cursor: pointer;
    padding: 22px 35px 23px;
    padding-left: 60px;
    transition: all 200ms linear;
    transition-delay: 0.1s;
}

.project-details__faq-box .faq-one-accrodion .accrodion-title h4 {
    margin: 0;
    font-size: 20px;
    font-weight: 600;
    line-height: 30px;
    color: var(--tecture-white);
    text-transform: uppercase;
    position: relative;
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
}

.project-details__faq-box .faq-one-accrodion .accrodion+.accrodion {
    margin-top: 16px;
}

.project-details__faq-box .faq-one-accrodion .accrodion.active .accrodion-title h4 {
    color: var(--tecture-white);
}

.project-details__faq-box .faq-one-accrodion .accrodion-title h4::before {
    content: "\e91e";
    font-family: 'icomoon' !important;
    font-weight: 700;
    font-size: 17px;
    color: var(--tecture-white);
    position: absolute;
    top: 50%;
    right: 0;
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 36px;
    height: 36px;
    background-color: #1d2019;
    border-radius: 3px;
    transform: translateY(-50%);
}

.project-details__faq-box .faq-one-accrodion .accrodion.active .accrodion-title h4::before {
    content: "\e921";
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
}

.project-details__faq-box .faq-one-accrodion .accrodion-content {
    position: relative;
    margin-top: -13px;
    padding-left: 30px;
    padding-right: 110px;
    padding-bottom: 26px;
    display: none;
}
.project-details__faq-box .faq-one-accrodion .accrodion.active .accrodion-content {
    display: block;
}

.project-details__faq-box .faq-one-accrodion .accrodion-content p {
    margin: 0;
}

.project-details__faq-box-count {
    position: absolute;
    top: 50%;
    left: 30px;
    transform: translateY(-50%);
}

.project-details__faq-box-count::before {
    position: relative;
    display: block;
    font-size: 20px;
    font-weight: 600;
    line-height: 30px;
    top: -1px;
    color: var(--tecture-white);
    font-family: var(--tecture-font-2);
    counter-increment: count;
    content: "0"counter(count);
    transition: all 200ms linear;
    transition-delay: 0.1s;
}

.project-details__faq-box-count::after {
    content: "";
    position: absolute;
    bottom: 9px;
    right: -3px;
    width: 3px;
    height: 3px;
    background-color: var(--tecture-white);
}

